:root{
    
    --cc-primary: #E52926;
    --cc-primary-light: #EE7573;
    --cc-primary-dark: #970200;
    --cc-primary-disabled: #EED1D1;
    --cc-secondary: #F8F8F0;
    --cc-white: #FFFFFF;
    --cc-black: #000000;
    --jwe-black-light: #555555;
}

body {
    font-family: 'Silka', sans-serif;
}

.nav-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
    position: fixed;
    width: 96%;
    height: 60px;
    padding: 0 2%;
    transition: background-color 0.3s ease;
}

.nav-bar.scrolled {
    background-color: var(--cc-black);
}

.nav-logo {
    height: 48px;
}

.nav-button {
    background-color: transparent;
    border: none;
    font-family: 'Silka', sans-serif;
    font-size: larger;
    margin: 0 8px;
    font-weight: bold;
    transition: color 0.5s ease;
}

.nav-button:hover {
    cursor: pointer;
    color: var(--cc-primary);
}

.nav-button-scrolled {
    color: var(--cc-secondary);
}

.lp-block1-wrapper, .lp-block2-wrapper, .lp-block3-wrapper, .lp-block4-wrapper {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lp-block1-wrapper {
    background-color: var(--cc-primary);
    background-image: url(/src/assets/images/cc6.JPEG);
    background-position: center;
    background-size: cover;
    justify-content: center;
}

.lp-block1-text-wrapper {
    width: 80%;
}

.lp-block1-text {
    font-size: 330%;
    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.4);
}

.lp-chevron-down-icon {
    margin-bottom: 10%;

}

.lp-block2-wrapper {
    background-color: var(--cc-secondary);
}

.lp-block3-wrapper {
    background-color: var(--cc-primary);
}

.lp-block4-wrapper {
    background-color: var(--cc-secondary);
}
